.img {
    width: 100%;
    height: 260px;
    object-fit: cover;
    border-radius: 2px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.img:hover {
    transform: scale(1.03);
    cursor: pointer;
}

.ul {
    display: grid;
    max-width: calc(95vw - 215px);
    grid-template-columns: repeat(auto-fill, minmax(165px, 1fr));
    grid-gap: 16px;
    margin-top: 10px;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}


.name {
    font-size: 15px;
}

.character {
    font-size: 10px;
}